.feedCard {
  position: relative;
  border-radius: 8px;
  text-align: left;

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__mockedImg {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    width: 100%;
    height: 100%;
  }

  h2 {
    transition: .2s;
  }

  &:hover {
    h2 {
      transition: .2s;
      color: #7e7e7e;
    }
  }

  .content {
    &__heading {
      margin: 0px;
    }

    &__category {
      position: relative;
      color: #FA6E03;

      &:after {
        content: '';
        height: 14px;
        width: 2px;
        background-color: #FA6E03;
        position: absolute;
        top: calc(50% - 7px);
        left: -16px;
      }
    }
  }
}

// Full SpecProjects
.fullSpecProjects {
  cursor: pointer;
}

// Photo Card
.photoFeedCard {
  border-radius: 8px;
  overflow: hidden;
  max-height: 498px;
  background-color: #FFFFFF;

  .mediaAmount {
    position: absolute;
    top: 4px;
    left: 4px;
  }

  &:hover {
    h2 {
      color: #c0c0c0;
    }
  }


  // sizes
  &_big {
    .content {
      &__heading {
        font-family: "PT Root UI Bold";
        font-size: 24px;
      }
    }
  }

  &_small {
    .content {
      &__heading {
        font-weight: 400;
        font-family: "PT Root UI Medium";
        font-size: 16px;
      }
    }
  }

  &__content {
    flex-grow: 0;
    color: white;
    position: absolute;
    bottom: 16px;
  }

  .content {
    &__heading {
      margin: 10px 0px;
    }
  }

  @media screen and (min-width: 768px) {
    &__content {
      padding: 0 36px;
    }
  }

  @media screen and (max-width: 767px) {
    &__content {
      padding: 0 16px 16px;
    }

    .content {
      &__heading {
        font-size: 20px;
        font-family: "PT Root UI Bold";
      }

      .feedInfo {
        svg path {
          fill: white;
        }

        .item {
          &__text {
            color: white;
          }
        }
      }
    }
  }
}

// Slider Card
.sliderFeedCard {
  height: 100%;

  &__slideButton {
    @media screen and (min-width: 320px) and (max-width: 767px) {
      svg {
        width: 9px;
        height: 18px;
      }
    }
  }

  .fullFeedCard {
    &__content {
      .content {
        &__heading {
          font-size: 24px;
          text-align: left;
        }
      }
    }
  }
}

